  border: none;
  background: none;
}
.offer__price-btn svg path {
  fill: var(--c-primary);
}
.offer__price-btn:hover svg path, .offer__price-btn:focus svg path, .offer__price-btn:active svg path {
  fill: var(--c-black);
}

.inside-slide {
  position: absolute;
  top: -10px;
  left: -10px;
  width: calc(100% + 20px);
  height: calc(100% + 20px);
  padding: 100px 10px 160px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  font-family: var(--f-secondary);
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.2s ease-in-out;
}
.inside-slide__table {
  border-top: 1px solid var(--c-white);
  border-left: 1px solid var(--c-white);
  border-spacing: 0;
  width: 100%;
}
.inside-slide__table td {
  width: 50%;
  height: 0;
  padding: 10px;
  border-right: 1px solid var(--c-white);
  border-bottom: 1px solid var(--c-white);
  font-weight: 300;
  color: var(--c-white);
}
.inside-slide__link {
  position: relative;
  color: var(--c-primary);
  font-weight: 700;
  transition: color 0.2s ease-in-out;
}
.inside-slide__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border: 1px solid var(--c-primary);
  transition: border 0.2s ease-in-out;
}
.inside-slide__link:hover, .inside-slide__link:focus, .inside-slide__link:active {
  color: var(--c-white);
}
.inside-slide__link:hover::before, .inside-slide__link:focus::before, .inside-slide__link:active::before {
  border: 1px solid var(--c-white);
}